@import url(https://cdn.jsdelivr.net/npm/bootstrap@5.1.1/dist/css/bootstrap.min.css);
@import url(https://cdn.jsdelivr.net/npm/bootstrap@5.1.0/dist/css/bootstrap.min.css);
@import url(https://use.fontawesome.com/releases/v5.7.2/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.loginPage .body {
  background-color: #e4e4e4;
  background-image: linear-gradient(
    to bottom,
    black,
    black 300px,
    #e4e4e4 300px,
    #e4e4e4
  ) !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  margin: 0;
  min-height: 100vh;
  min-width: 100vw;
}

.loginPage .user_card {
  min-height: 350px;
  max-width: 400px;
  margin-top: auto;
  margin-bottom: auto;
  background: #fff;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
}
.loginPage .login_name_wrapper {
  height: 20% !important;
  min-height: 200px;
  margin-bottom: auto;
  margin-top: auto;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #fff;
  font-size: 35px;
  font-weight: bold;
  text-transform: uppercase;
}
.loginPage .login_logo_container {
  position: absolute;
  height: 170px;
  width: 170px;
  top: -75px;
  border-radius: 50%;
  /* background: black; */
  background: black;

  padding: 10px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loginPage .login_logo {
  /* height: 150px;
  width: 150px;
  border-radius: 50%; */
  height: 80%;
  width: 80%;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.loginPage .form_container {
  margin-top: 100px;
}
.loginPage .login_btn {
  width: 100%;
  background: black !important;
  color: white !important;
}
.loginPage .login_btn:hover {
  /* background: #2c44b0 !important; */
  background-color: #cbb26c !important;
  color: black !important;
}

.loginPage .login_container {
  padding: 0 2rem;
}
.loginPage .input_group_text {
  background: black !important;
  color: white !important;
  border: 0 !important;
  border-radius: 0.25rem 0 0 0.25rem !important;
}
.loginPage .input_user,
.loginPage .input_pass:focus {
  box-shadow: none !important;
  outline: 0px !important;
}
.loginPage
  .custom-checkbox
  .custom-control-input:checked
  ~ .custom-control-label::before {
  background-color: black !important;
}
.loginPage.input_group_text {
  height: 38px;
}

.spinner {
    /* display: inline-block; */
    margin: auto;
    width: 20px;
    height: 20px;
    background-color: #f1f1f9;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: 5px solid black;
    border-bottom-color: transparent;
    position: relative;
    animation: spinner 1s linear infinite paused;
    -webkit-animation: spinner 1s linear infinite;
  }
  
  @keyframes spinner {
    to {
      transform: rotate(1turn);
      -webkit-transform: rotate(1turn);
      -moz-transform: rotate(1turn);
      -ms-transform: rotate(1turn);
      -o-transform: rotate(1turn);
    }
  }
  
.pageheader img{
        width: 200px;
        height: 200px;
}

.pageheader .ant-table-cell{
    text-align: center;
}
